import { adapters } from "@rp/adapters"

type UploadFileCommonData = {
  fileId: string
  ownerId: string
  updatedById: string
  origin: "api" | "web" | "native"
}

type UploadFileData = UploadFileCommonData & {
  rawFileSize: number
  uploadLocation: "filesPage" | "mainChatModal" | "activeChatModal"
  uploadTimestamp: number
  resultsTimeStamp: number
  uploadDuration: number
  uploadError: string
  mimeType: string
}

type FileEditedData = UploadFileCommonData & {
  updatedFields: Array<"fileName" | "sharedWith">
  sharedWith: Array<string> | null
  timestamp: number
}

type FileDeletedData = UploadFileCommonData & {
  timestamp: number
}

export const filesCustomerIo = {
  filesApi: {
    uploaded(data: UploadFileData) {
      adapters.customerIo.track({
        name: "file",
        data: {
          ...data,
          action: "uploaded",
        },
      })
    },
    edited(data: FileEditedData) {
      adapters.customerIo.track({
        name: "file",
        data: {
          ...data,
          action: "edited",
        },
      })
    },
    deleted(data: FileDeletedData) {
      adapters.customerIo.track({
        name: "file",
        data: {
          ...data,
          action: "deleted",
        },
      })
    },
  },
}
